<template>
    <header class="watch-header" :class="{'watch-header--video': playback_mode === 'Video'}">
        <router-link class="watch-header__back-icon" v-ripple :to="playback_mode === 'Live' ? '/tv/' : '/video/'">
            <Icon icon="fluent:arrow-left-12-filled" width="25px" />
        </router-link>
        <img class="watch-header__broadcaster" v-if="playback_mode === 'Live'"
            :src="`${Utils.api_base_url}/channels/${channelsStore.channel.current.id}/logo`">
        <span class="watch-header__program-title" v-html="ProgramUtils.decorateProgramInfo(
            playback_mode === 'Live' ? channelsStore.channel.current.program_present : playerStore.recorded_program, 'title'
        )"></span>
        <span class="watch-header__program-time">
            {{ProgramUtils.getProgramTime(playback_mode === 'Live' ? channelsStore.channel.current.program_present : playerStore.recorded_program, true)}}
        </span>
        <v-spacer></v-spacer>
        <span class="watch-header__now">{{time}}</span>
    </header>
</template>
<script lang="ts">

import { mapStores } from 'pinia';
import { defineComponent, PropType } from 'vue';

import useChannelsStore from '@/stores/ChannelsStore';
import usePlayerStore from '@/stores/PlayerStore';
import Utils, { dayjs, ProgramUtils } from '@/utils';

export default defineComponent({
    name: 'Watch-Header',
    props: {
        playback_mode: {
            type: String as PropType<'Live' | 'Video'>,
            required: true,
        },
    },
    data() {
        return {

            // 
            Utils: Object.freeze(Utils),
            ProgramUtils: Object.freeze(ProgramUtils),

            // 
            time: dayjs().format(Utils.isSmartphoneHorizontal() ? 'HH:mm:ss' : 'YYYY/MM/DD HH:mm:ss'),

            //  ID
            time_interval_id: 0,
        };
    },
    computed: {
        ...mapStores(useChannelsStore, usePlayerStore),
    },
    created() {
        // 1
        this.time_interval_id = window.setInterval(() => {
            this.time = dayjs().format(Utils.isSmartphoneHorizontal() ? 'HH:mm:ss' : 'YYYY/MM/DD HH:mm:ss');
        }, 1 * 1000);
    },
    beforeUnmount() {
        // 
        window.clearInterval(this.time_interval_id);
    },
});

</script>
<style lang="scss" scoped>

.watch-header {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 82px;
    padding-left: calc(68px + 30px);
    padding-right: 30px;
    background: linear-gradient(to bottom, #000000cf, transparent);
    transition: opacity 0.3s, visibility 0.3s;
    opacity: 0;
    visibility: hidden;
    z-index: 5;

    @include tablet-vertical {
        height: 66px;
        padding-left: 16px;
        padding-right: 16px;
    }
    @include smartphone-horizontal {
        padding-left: calc(68px + 16px);
        padding-right: 16px;
    }
    @include smartphone-horizontal {
        height: 66px;
        padding-left: calc(0px + 16px);
    }
    @include smartphone-vertical {
        height: 50px;
        padding-left: 16px;
        padding-right: 16px;
    }

    &.watch-header--video {
        .watch-header__program-time {
            font-size: 13px;
        }
        .watch-header__now {
            display: none;
        }
    }

    .watch-header__back-icon {
        display: none;
        @include tablet-vertical {
            display: flex;
            position: relative !important;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 40px;
            height: 40px;
            left: -6px;
            padding: 6px;
            margin-right: 2px;
            border-radius: 50%;
            color: var(--v-text-base);
        }
        @include smartphone-horizontal {
            display: flex;
            position: relative !important;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 36px;
            height: 36px;
            left: -6px;
            padding: 6px;
            margin-right: 2px;
            border-radius: 50%;
            color: var(--v-text-base);
        }
        @include smartphone-vertical {
            display: flex;
            position: relative !important;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 36px;
            height: 36px;
            left: -6px;
            padding: 6px;
            margin-right: 2px;
            border-radius: 50%;
            color: var(--v-text-base);
        }
    }

    .watch-header__broadcaster {
        display: inline-block;
        flex-shrink: 0;
        width: 64px;
        height: 36px;
        margin-right: 18px;
        border-radius: 5px;
        background: linear-gradient(150deg, var(--v-gray-base), var(--v-background-lighten2));
        object-fit: cover;
        user-select: none;

        @include tablet-vertical {
            width: 48px;
            height: 28px;
            margin-right: 12px;
            border-radius: 4px;
        }
        @include smartphone-horizontal {
            width: 36px;
            height: 28px;
            margin-right: 12px;
            border-radius: 4px;
        }
        @include smartphone-vertical {
            display: none;
            margin-right: 0px;
        }
    }

    .watch-header__program-title {
        font-size: 18px;
        font-weight: bold;
        font-feature-settings: "palt" 1;  // 
        letter-spacing: 0.05em;  // 
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;

        @include tablet-vertical {
            font-size: 16px;
        }
        @include smartphone-horizontal {
            font-size: 16px;
        }
        @include smartphone-vertical {
            font-size: 14px;
        }
    }

    .watch-header__program-time {
        flex-shrink: 0;
        margin-left: 16px;
        font-size: 15px;
        font-weight: 500;

        @include smartphone-horizontal {
            margin-left: 8px;
            font-size: 14px;
        }
        @include smartphone-vertical {
            margin-left: 8px;
            font-size: 13px;
        }
    }

    .watch-header__now {
        flex-shrink: 0;
        margin-left: 16px;
        font-size: 13px;
        font-weight: 500;

        @include smartphone-horizontal {
            margin-left: 8px;
        }
        @include smartphone-vertical {
            display: none;
        }
    }
}

</style>