
import { ChannelType } from '@/services/Channels';


/**
 * 
 */
export class ChannelUtils {

    /**
     * display_channel_id 
     * @param display_channel_id display_channel_id
     * @returns  ( display_channel_id  null)
     */
    static getChannelType(display_channel_id: string): ChannelType | null {
        try {
            const result = display_channel_id.match('(?<channel_type>[a-z]+)[0-9]+')!.groups!.channel_type.toUpperCase();
            switch (result) {
                case 'GR': return 'GR';
                case 'BS': return 'BS';
                case 'CS': return 'CS';
                case 'CATV': return 'CATV';
                case 'SKY': return 'SKY';
                case 'STARDIGIO': return 'STARDIGIO';
                // ChannelType 
                default: return null;
            }
        } catch (e) {
            //  display_channel_id 
            return null;
        }
    }


    /**
     * 
     * ref: https://ja.wikipedia.org/wiki/%E3%83%8B%E3%82%B3%E3%83%8B%E3%82%B3%E5%AE%9F%E6%B3%81
     * @param jikkyo_force 
     * @returns normalor manyor so-manyor festival
     */
    static getChannelForceType(jikkyo_force: number | null): 'normal' | 'many' | 'so-many' | 'festival' {

        //  null=
        if (jikkyo_force === null) return 'normal';

        //  500 
        if (jikkyo_force >= 500) return 'festival';
        //  200 
        if (jikkyo_force >= 200) return 'so-many';
        //  100 
        if (jikkyo_force >= 100) return 'many';

        // 
        return 'normal';
    }


    /**
     * 
     *  + BS 
     * @param channel_name 
     * @returns  ( null)
     */
    static getChannelHashtag(channel_name: string): string | null {
        // NHK
        if (channel_name.startsWith('NHK')) {
            return '#nhk';
        } else if (channel_name.startsWith('NHKE')) {
            return '#etv';
        // 
        } else if (channel_name.startsWith('')) {
            return '#ntv';
        } else if (channel_name.startsWith('')) {
            return '#ytv';
        } else if (channel_name.startsWith('')) {
            return '#chukyotv';
        } else if (channel_name.startsWith('')) {
            return '#tvasahi';
        } else if (channel_name.startsWith('ABC')) {
            return '#abc';
        } else if (channel_name.startsWith('~') || channel_name.startsWith('')) {
            return '#nagoyatv';
        } else if (channel_name.startsWith('TBS') && !channel_name.includes('TBS')) {
            return '#tbs';
        } else if (channel_name.startsWith('MBS')) {
            return '#mbs';
        } else if (channel_name.startsWith('CBC')) {
            return '#cbc';
        } else if (channel_name.startsWith('')) {
            return '#tvtokyo';
        } else if (channel_name.startsWith('')) {
            return '#tvo';
        } else if (channel_name.startsWith('')) {
            return '#tva';
        } else if (channel_name.startsWith('')) {
            return '#fujitv';
        } else if (channel_name.startsWith('')) {
            return '#kantele';
        } else if (channel_name.startsWith('')) {
            return '#tokaitv';
        // 
        } else if (channel_name.startsWith('TOKYO MX')) {
            return '#tokyomx';
        } else if (channel_name.startsWith('tvk')) {
            return '#tvk';
        } else if (channel_name.startsWith('')) {
            return '#chibatv';
        } else if (channel_name.startsWith('')) {
            return '#teletama';
        } else if (channel_name.startsWith('')) {
            return '#suntv';
        } else if (channel_name.startsWith('KBS')) {
            return '#kbs';
        // BSCS
        } else if (channel_name.startsWith('NHKBS1')) {
            return '#nhkbs1';
        } else if (channel_name.startsWith('NHKBS')) {
            return '#nhkbsp';
        } else if (channel_name.startsWith('BS')) {
            return '#bsntv';
        } else if (channel_name.startsWith('BS')) {
            return '#bsasahi';
        } else if (channel_name.startsWith('BS-TBS')) {
            return '#bstbs';
        } else if (channel_name.startsWith('BS')) {
            return '#bstvtokyo';
        } else if (channel_name.startsWith('BS')) {
            return '#bsfuji';
        } else if (channel_name.startsWith('BS11')) {
            return '#bs11';
        } else if (channel_name.startsWith('BS12')) {
            return '#bs12';
        } else if (channel_name.startsWith('AT-X')) {
            return '#at_x';
        }

        return null;
    }
}
