<template>
    <!--  -->
    <SettingsBase>
        <h2 class="settings__heading">
            <router-link v-ripple class="settings__back-button" to="/settings/">
                <Icon icon="fluent:arrow-left-12-filled" width="25px" />
            </router-link>
            <Icon icon="fluent:video-clip-multiple-16-filled" width="26px" />
            <span class="ml-3"></span>
        </h2>
        <v-tabs-fix class="settings__tab" background-color="transparent" centered v-model="tab">
            <v-tab style="text-transform: none !important;" v-for="network_circuit in network_circuits" :key="network_circuit">
                {{network_circuit}}
            </v-tab>
        </v-tabs-fix>
        <v-tabs-items-fix v-model="tab">
            <v-tab-item-fix class="settings__content mt-0" v-for="network_circuit in network_circuits" :key="network_circuit">
                <div class="settings__item settings__item--sync-disabled">
                    <div class="settings__item-heading"></div>
                    <div class="settings__item-label">
                        <br>
                        <br>
                    </div>
                    <div class="settings__item-label mt-1">
                         [1080p (60fps)] <b> 30fps (60i) </b><br>
                    </div>
                    <div class="settings__item-label mt-1">
                        Fire HD 10 (2021)  Android ( MediaTek SoC ) 1080p  720p <br>
                    </div>
                    <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense" v-if="network_circuit !== ''"
                        :items="tv_streaming_quality" v-model="settingsStore.settings.tv_streaming_quality">
                    </v-select>
                    <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense" v-if="network_circuit === ''"
                        :items="tv_streaming_quality_cellular" v-model="settingsStore.settings.tv_streaming_quality_cellular">
                    </v-select>
                </div>
                <div class="settings__item settings__item--switch settings__item--sync-disabled"
                    :class="{'settings__item--disabled': PlayerUtils.isHEVCVideoSupported() === false}">
                    <label class="settings__item-heading" :for="`tv_data_saver_mode${network_circuit === '' ? '_cellular' : ''}`">
                        
                    </label>
                    <label class="settings__item-label" :for="`tv_data_saver_mode${network_circuit === '' ? '_cellular' : ''}`">
                         H.265 / HEVC <b> 50%  70% </b>  PC <br>
                    </label>
                    <div class="settings__item-label mt-1">
                         (4G/5G) Wi-Fi <br>
                        <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === false">
                            
                        </p>
                        <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === true">
                             Firefox 
                        </p>
                    </div>
                    <v-switch class="settings__item-switch" id="tv_data_saver_mode" inset hide-details v-if="network_circuit !== ''"
                        v-model="settingsStore.settings.tv_data_saver_mode" :disabled="PlayerUtils.isHEVCVideoSupported() === false">
                    </v-switch>
                    <v-switch class="settings__item-switch" id="tv_data_saver_mode_cellular" inset hide-details v-if="network_circuit === ''"
                        v-model="settingsStore.settings.tv_data_saver_mode_cellular" :disabled="PlayerUtils.isHEVCVideoSupported() === false">
                    </v-switch>
                </div>
                <div class="settings__item settings__item--switch settings__item--sync-disabled">
                    <label class="settings__item-heading" :for="`tv_low_latency_mode${network_circuit === '' ? '_cellular' : ''}`">
                        
                    </label>
                    <label class="settings__item-label" :for="`tv_low_latency_mode${network_circuit === '' ? '_cellular' : ''}`">
                        <b> 0.9 </b><br>
                         3  (1.1x) <br>
                    </label>
                    <div class="settings__item-label mt-1">
                         Wi-Fi <br>
                    </div>
                    <v-switch class="settings__item-switch" id="tv_low_latency_mode" inset hide-details v-if="network_circuit !== ''"
                        v-model="settingsStore.settings.tv_low_latency_mode">
                    </v-switch>
                    <v-switch class="settings__item-switch" id="tv_low_latency_mode_cellular" inset hide-details v-if="network_circuit === ''"
                        v-model="settingsStore.settings.tv_low_latency_mode_cellular">
                    </v-switch>
                </div>
                <v-divider class="mt-6"></v-divider>
                <div class="settings__item settings__item--sync-disabled">
                    <div class="settings__item-heading"></div>
                    <div class="settings__item-label">
                        <br>
                        <br>
                    </div>
                    <div class="settings__item-label mt-1">
                         [1080p (60fps)] <b> 30fps (60i) </b><br>
                    </div>
                    <div class="settings__item-label mt-1">
                        Fire HD 10 (2021)  Android ( MediaTek SoC ) 1080p  720p <br>
                    </div>
                    <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense" v-if="network_circuit !== ''"
                        :items="video_streaming_quality" v-model="settingsStore.settings.video_streaming_quality">
                    </v-select>
                    <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense" v-if="network_circuit === ''"
                        :items="video_streaming_quality_cellular" v-model="settingsStore.settings.video_streaming_quality_cellular">
                    </v-select>
                </div>
                <div class="settings__item settings__item--switch settings__item--sync-disabled"
                    :class="{'settings__item--disabled': PlayerUtils.isHEVCVideoSupported() === false}">
                    <label class="settings__item-heading" :for="`video_data_saver_mode${network_circuit === '' ? '_cellular' : ''}`">
                        
                    </label>
                    <label class="settings__item-label" :for="`video_data_saver_mode${network_circuit === '' ? '_cellular' : ''}`">
                         H.265 / HEVC <b> 50%  70% </b>  PC <br>
                    </label>
                    <div class="settings__item-label mt-1">
                         (4G/5G) Wi-Fi <br>
                        <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === false">
                            
                        </p>
                        <p class="mt-1 mb-0 error--text lighten-1" v-if="PlayerUtils.isHEVCVideoSupported() === false && Utils.isFirefox() === true">
                             Firefox 
                        </p>
                    </div>
                    <v-switch class="settings__item-switch" id="video_data_saver_mode" inset hide-details v-if="network_circuit !== ''"
                        v-model="settingsStore.settings.video_data_saver_mode" :disabled="PlayerUtils.isHEVCVideoSupported() === false">
                    </v-switch>
                    <v-switch class="settings__item-switch" id="video_data_saver_mode_cellular" inset hide-details v-if="network_circuit === ''"
                        v-model="settingsStore.settings.video_data_saver_mode_cellular" :disabled="PlayerUtils.isHEVCVideoSupported() === false">
                    </v-switch>
                </div>
            </v-tab-item-fix>
        </v-tabs-items-fix>
    </SettingsBase>
</template>
<script lang="ts">

import { mapStores } from 'pinia';
import { defineComponent } from 'vue';

import useSettingsStore from '@/stores/SettingsStore';
import Utils, { PlayerUtils } from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

const QUALITY_H264 = [
    {text: '1080p (60fps) (4.50GB/h / 10.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (4.50GB/h / 10.0Mbps)', value: '1080p'},
    {text: '810p (2.62GB/h / 5.8Mbps)', value: '810p'},
    {text: '720p (2.18GB/h / 4.9Mbps)', value: '720p'},
    {text: '540p (1.52GB/h / 3.4Mbps)', value: '540p'},
    {text: '480p (1.06GB/h / 2.3Mbps)', value: '480p'},
    {text: '360p (0.60GB/h / 1.3Mbps)', value: '360p'},
    {text: '240p (0.35GB/h / 0.8Mbps)', value: '240p'},
];

const QUALITY_H265 = [
    {text: '1080p (60fps) (1.80GB/h / 4.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (1.37GB/h / 3.0Mbps)', value: '1080p'},
    {text: '810p (1.05GB/h / 2.3Mbps)', value: '810p'},
    {text: '720p (0.82GB/h / 1.8Mbps)', value: '720p'},
    {text: '540p (0.53GB/h / 1.2Mbps)', value: '540p'},
    {text: '480p (0.46GB/h / 1.0Mbps)', value: '480p'},
    {text: '360p (0.30GB/h / 0.7Mbps)', value: '360p'},
    {text: '240p (0.20GB/h / 0.4Mbps)', value: '240p'},
];

export default defineComponent({
    name: 'Settings-Quality',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            Utils: Object.freeze(Utils),
            PlayerUtils: Object.freeze(PlayerUtils),

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            tab: null as number | null,

            // 
            network_circuits: PlayerUtils.getNetworkCircuitType() !== null ? ['Wi-Fi ', ''] : ['Wi-Fi'],

            // 
            tv_streaming_quality: QUALITY_H264,
            tv_streaming_quality_cellular: QUALITY_H264,

            // 
            video_streaming_quality: QUALITY_H264,
            video_streaming_quality_cellular: QUALITY_H264,
        };
    },
    computed: {
        ...mapStores(useSettingsStore),
    },
    watch: {
        'settingsStore.settings.tv_data_saver_mode': {
            immediate: true,
            handler(value: boolean) {
                if (value === true) {
                    this.tv_streaming_quality = QUALITY_H265;
                } else {
                    this.tv_streaming_quality = QUALITY_H264;
                }
            },
        },
        'settingsStore.settings.tv_data_saver_mode_cellular': {
            immediate: true,
            handler(value: boolean) {
                if (value === true) {
                    this.tv_streaming_quality_cellular = QUALITY_H265;
                } else {
                    this.tv_streaming_quality_cellular = QUALITY_H264;
                }
            },
        },
        'settingsStore.settings.video_data_saver_mode': {
            immediate: true,
            handler(value: boolean) {
                if (value === true) {
                    this.video_streaming_quality = QUALITY_H265;
                } else {
                    this.video_streaming_quality = QUALITY_H264;
                }
            },
        },
        'settingsStore.settings.video_data_saver_mode_cellular': {
            immediate: true,
            handler(value: boolean) {
                if (value === true) {
                    this.video_streaming_quality_cellular = QUALITY_H265;
                } else {
                    this.video_streaming_quality_cellular = QUALITY_H264;
                }
            },
        },
    },
    created() {
        //  H.265 
        if (this.settingsStore.settings.tv_data_saver_mode === true) {
            this.tv_streaming_quality = QUALITY_H265;
        }
        if (this.settingsStore.settings.tv_data_saver_mode_cellular === true) {
            this.tv_streaming_quality_cellular = QUALITY_H265;
        }
        if (this.settingsStore.settings.video_data_saver_mode === true) {
            this.video_streaming_quality = QUALITY_H265;
        }
        if (this.settingsStore.settings.video_data_saver_mode_cellular === true) {
            this.video_streaming_quality_cellular = QUALITY_H265;
        }
    }
});

</script>
<style lang="scss" scoped>

.settings__tab {
    position: sticky;
    top: 65px;
    z-index: 4;
    background: var(--v-background-lighten1);
    @include smartphone-horizontal {
        top: 0px;
    }
    @include smartphone-vertical {
        top: 60px;
        background: var(--v-background-base);
    }
}

</style>