                cursor: pointer;
            }
        }

        .hashtag-container {
            display: flex;
            flex-direction: column;

            .hashtag {
                display: flex;
                position: relative !important;
                align-items: center;
                padding-top: 1.5px;
                padding-bottom: 1.5px;
                padding-left: 8px;
                padding-right: 4px;
                border-radius: 7px;
                transition: background-color 0.15s ease;
                cursor: pointer;
                @include smartphone-horizontal {
                    padding-top: 0px;
                    padding-bottom: 0px;
                    padding-left: 4px;
                    padding-right: 2px;
                }
                @include smartphone-vertical {
                    padding-top: 0px;
                    padding-bottom: 0px;
                }
                &:first-of-type {
                    margin-top: 6px;
                    @include smartphone-vertical {
                        margin-top: 0px;
                    }
                }
                &:hover {
                    background: rgba(255, 255, 255, 10%);
                }
                //  hover 
                @media (hover: none) {
                    &:hover {
                        background: transparent;
                    }
                }

                &--editing {
                    &:hover {
                        background: transparent;
                    }
                    .hashtag__input {
                        box-shadow: rgba(79, 130, 230, 60%) 0 0 0 3.5px;
                        cursor: text;
                        pointer-events: auto;
                    }
                }

                &__input {
                    display: block;
                    flex-grow: 1;
                    border-radius: 2px;
                    color: var(--v-twitter-lighten2);
                    opacity: 1;
                    outline: none;
                    cursor: pointer;
                    transition: box-shadow 0.09s ease;
                    margin-right: 4px;
                    font-size: 12.5px;
                    pointer-events: none;
                    // iOS Safari 
                    @supports (-webkit-touch-callout: none) {
                        @include smartphone-horizontal {
                            position: absolute !important;
                            left: -26px !important;
                            width: calc(100% - 6px);
                            margin-right: 0px;
                            font-size: 16px;
                            transform: scale(0.78125);
                        }
                        @include smartphone-vertical {
                            position: absolute !important;
                            left: -26px !important;
                            width: calc(100% - 21px);
                            margin-right: 0px;
                            font-size: 16px;
                            transform: scale(0.78125);
                        }
                    }
                }

                &__edit-button {
                    margin-left: auto;
                }
                &__edit-button, &__delete-button, &__sort-handle {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 19px;
                    height: 27px;
                    border-radius: 5px;
                    outline: none;
                    cursor: pointer;
                    @include smartphone-vertical {
                        width: 25px;
                    }
                }
                &__sort-handle {
                    cursor: move;
                }
            }
        }
    }

    .twitter-account-list {
        position: absolute;
        left: 12px;
        right: 12px;
        bottom: 48px;
        max-height: calc(100vh - 137px);
        max-height: calc(100dvh - 137px);
        border-radius: 7px;
        // 
        clip-path: inset(0% 0% 0% 0% round 7px);
        background: var(--v-background-lighten2);
        box-shadow: 0px 3px 4px rgba(0, 0, 0, 53%);
        transition: opacity 0.15s ease, visibility 0.15s ease;
        opacity: 0;
        visibility: hidden;
        overflow-y: auto;
        z-index: 3;
        @include tablet-vertical {
            left: 24px;
            right: 24px;
        }
        @include smartphone-horizontal {
            left: 8px;
            right: 8px;
            bottom: 40px;
            max-height: calc(100vh - 82px);
            max-height: calc(100dvh - 82px);
            border-radius: 6px;
            clip-path: inset(0% 0% 0% 0% round 6px);
        }
        @include smartphone-vertical {
            bottom: 32px;
            max-height: calc(100% - 40px);
            border-radius: 6px;
            clip-path: inset(0% 0% 0% 0% round 6px);
        }
        &--display {
            opacity: 1;
            visibility: visible;
        }
        &::-webkit-scrollbar-track {
            background: var(--v-background-lighten2);
        }
        &::-webkit-scrollbar-thumb {
                background: var(--v-gray-base);
            &:hover {
                background: var(--v-gray-base);
            }
        }

        .twitter-account {
            display: flex;
            align-items: center;
            padding: 12px 12px;
            border-radius: 7px;
            user-select: none;
            cursor: pointer;
            @include smartphone-horizontal {
                padding: 8px 12px;
            }
            @include smartphone-vertical {
                padding: 8px 12px;
            }

            &__icon {
                display: block;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                @include smartphone-horizontal {
                    width: 36px;
                    height: 36px;
                }
                @include smartphone-vertical {
                    width: 36px;
                    height: 36px;
                }
            }
            &__info {
                display: flex;
                flex-direction: column;
                flex-grow: 1;
                min-width: 0;
                margin-left: 12px;
            }
            &__name {
                font-size: 17px;
                font-weight: bold;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                @include smartphone-horizontal {
                    font-size: 14px;
                    line-height: 1.3;
                }
                @include smartphone-vertical {
                    font-size: 14px;
                    line-height: 1.3;
                }
            }
            &__screen-name {
                color: var(--v-text-darken1);
                font-size: 14px;
                @include smartphone-horizontal {
                    font-size: 13px;
                }
                @include smartphone-vertical {
                    font-size: 13px;
                }
            }
            &__check {
                flex-shrink: 0;
                color: var(--v-twitter-lighten1);
            }
        }
    }
}

