<template>
    <!--  -->
    <SettingsBase>
        <h2 class="settings__heading">
            <router-link v-ripple class="settings__back-button" to="/settings/">
                <Icon icon="fluent:arrow-left-12-filled" width="25px" />
            </router-link>
            <Icon icon="fa-brands:twitter" width="22px" />
            <span class="ml-3">Twitter</span>
        </h2>
        <div class="settings__content" :class="{'settings__content--loading': is_loading}">
            <div class="twitter-accounts">
                <div class="twitter-accounts__heading" v-if="userStore.user !== null && userStore.user.twitter_accounts.length > 0">
                    <Icon icon="fluent:person-board-20-filled" class="mr-2" height="30" />
                </div>
                <div class="twitter-accounts__guide" v-if="userStore.user === null || userStore.user.twitter_accounts.length === 0">
                    <Icon class="flex-shrink-0" icon="fa-brands:twitter" width="45px" />
                    <div class="ml-4">
                        <div class="font-weight-bold text-h6">Twitter </div>
                        <div class="text--text text--darken-1 text-subtitle-2 mt-1">
                            Twitter  Twitter 
                        </div>
                    </div>
                </div>
                <div class="twitter-account"
                    v-for="twitter_account in (userStore.user !== null ? userStore.user.twitter_accounts: [])"
                    :key="twitter_account.id">
                    <img class="twitter-account__icon" :src="twitter_account.icon_url">
                    <div class="twitter-account__info">
                        <div class="twitter-account__info-name">
                            <span class="twitter-account__info-name-text">{{twitter_account.name}}</span>
                        </div>
                        <span class="twitter-account__info-screen-name">
                            @{{twitter_account.screen_name}}
                            <span v-if="twitter_account.is_oauth_session === true">(Legacy Session)</span>
                        </span>
                    </div>
                    <v-btn class="twitter-account__logout ml-auto" width="124" height="52" depressed
                        @click="logoutTwitterAccount(twitter_account.screen_name)">
                        <Icon icon="fluent:plug-disconnected-20-filled" class="mr-2" height="24" />
                    </v-btn>
                </div>
                <v-btn class="twitter-account__login" color="secondary" max-width="250" height="50" depressed
                    @click="loginTwitterAccountWithPasswordForm()">
                    <Icon icon="fluent:plug-connected-20-filled" class="mr-2" height="24" />
                </v-btn>
                <v-dialog max-width="600" v-model="twitter_password_auth_dialog">
                    <v-card>
                        <v-card-title class="justify-center pt-6 font-weight-bold">Twitter </v-card-title>
                        <!--  -->
                        <v-card-text class="pt-2 pb-0">
                            <p class="mb-1">2023/07 Twitter  API  Twitter </p>
                            <p class="mb-1"> KonomiTV  <a href="https://github.com/tsukumijima/tweepy-authlib" target="_blank"></a>  Twitter  (2) </p>
                            <p class="mb-1"> Cookie  KonomiTV Cookie  Twitter API </p>
                            <p class="mb-1"></p>
                            <v-form class="settings__item" ref="twitter_form" @submit.prevent>
                                <v-text-field class="settings__item-form mt-6" outlined label=" (@  ID)" placeholder="screen_name"
                                    ref="twitter_screen_name"
                                    :dense="is_form_dense"
                                    v-model="twitter_screen_name"
                                    :rules="[(value) => !!value || '']">
                                </v-text-field>
                                <v-text-field class="settings__item-form" outlined label=""
                                    :dense="is_form_dense"
                                    v-model="twitter_password"
                                    :type="twitter_password_showing ? 'text' : 'password'"
                                    :append-icon="twitter_password_showing ? 'mdi-eye' : 'mdi-eye-off'"
                                    :rules="[(value) => !!value || '']"
                                    @click:append="twitter_password_showing = !twitter_password_showing">
                                </v-text-field>
                            </v-form>
                        </v-card-text>
                        <v-card-actions class="pt-0 px-6 pb-5">
                            <v-spacer></v-spacer>
                            <v-btn color="text" height="40" text @click="twitter_password_auth_dialog = false"></v-btn>
                            <v-btn color="secondary" height="40" class="px-4" @click="loginTwitterAccountWithPassword()"></v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </div>
            <div class="settings__item settings__item--switch">
                <label class="settings__item-heading" for="fold_panel_after_sending_tweet"></label>
                <label class="settings__item-label" for="fold_panel_after_sending_tweet">
                    <br>
                    <br>
                </label>
                <v-switch class="settings__item-switch" id="fold_panel_after_sending_tweet" inset hide-details
                    v-model="settingsStore.settings.fold_panel_after_sending_tweet">
                </v-switch>
            </div>
            <div class="settings__item settings__item--switch">
                <label class="settings__item-heading" for="reset_hashtag_when_program_switches"></label>
                <label class="settings__item-label" for="reset_hashtag_when_program_switches">
                    <br>
                    <br>
                </label>
                <v-switch class="settings__item-switch" id="reset_hashtag_when_program_switches" inset hide-details
                    v-model="settingsStore.settings.reset_hashtag_when_program_switches">
                </v-switch>
            </div>
            <div class="settings__item settings__item--switch">
                <label class="settings__item-heading" for="auto_add_watching_channel_hashtag"></label>
                <label class="settings__item-label" for="auto_add_watching_channel_hashtag">
                     (#nhk, #tokyomx ) <br>
                    BS AT-X <br>
                    <br>
                </label>
                <v-switch class="settings__item-switch" id="auto_add_watching_channel_hashtag" inset hide-details
                    v-model="settingsStore.settings.auto_add_watching_channel_hashtag">
                </v-switch>
            </div>
            <div class="settings__item">
                <div class="settings__item-heading"> Twitter </div>
                <div class="settings__item-label">
                     Twitter <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="twitter_active_tab" v-model="settingsStore.settings.twitter_active_tab">
                </v-select>
            </div>
            <div class="settings__item">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="tweet_hashtag_position" v-model="settingsStore.settings.tweet_hashtag_position">
                </v-select>
            </div>
            <div class="settings__item">
                <div class="settings__item-heading"></div>
                <div class="settings__item-label">
                    <br>
                      <br>
                </div>
                <v-select class="settings__item-form" outlined hide-details :dense="is_form_dense"
                    :items="tweet_capture_watermark_position" v-model="settingsStore.settings.tweet_capture_watermark_position">
                </v-select>
            </div>
        </div>
        <v-overlay :value="is_twitter_password_auth_sending" z-index="300">
            <v-progress-circular color="secondary" indeterminate size="64" />
        </v-overlay>
    </SettingsBase>
</template>
<script lang="ts">

import { mapStores } from 'pinia';
import { defineComponent } from 'vue';

import Message from '@/message';
import Twitter from '@/services/Twitter';
import useSettingsStore from '@/stores/SettingsStore';
import useUserStore from '@/stores/UserStore';
import Utils from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

export default defineComponent({
    name: 'Settings-Twitter',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            twitter_active_tab: [
                {text: '', value: 'Search'},
                {text: '', value: 'Timeline'},
                {text: '', value: 'Capture'},
            ],

            // 
            tweet_hashtag_position: [
                {text: '', value: 'Prepend'},
                {text: '', value: 'Append'},
                {text: '', value: 'PrependWithLineBreak'},
                {text: '', value: 'AppendWithLineBreak'},
            ],

            // 
            tweet_capture_watermark_position: [
                {text: '', value: 'None'},
                {text: '', value: 'TopLeft'},
                {text: '', value: 'TopRight'},
                {text: '', value: 'BottomLeft'},
                {text: '', value: 'BottomRight'},
            ],

            // 
            is_loading: true,

            // 
            is_twitter_password_auth_sending: false,

            // 
            twitter_password_auth_dialog: false,

            // Twitter 
            twitter_screen_name: '',
            twitter_password: '',
            twitter_password_showing: false,
        };
    },
    computed: {
        ...mapStores(useSettingsStore, useUserStore),
    },
    async created() {

        // 
        await this.userStore.fetchUser();

        // 
        this.is_loading = false;
    },
    methods: {
        async loginTwitterAccountWithPasswordForm() {
            // 
            if (this.userStore.is_logged_in === false) {
                Message.warning('KonomiTV ');
                await Utils.sleep(0.01);
                this.twitter_password_auth_dialog = false;
                return;
            }
            this.twitter_password_auth_dialog = true;
        },

        async loginTwitterAccountWithPassword() {

            // 
            if ((this.$refs.twitter_form as any).validate() === false) {
                return;
            }

            // Twitter  API 
            this.is_twitter_password_auth_sending = true;
            const result = await Twitter.authWithPassword({
                screen_name: this.twitter_screen_name,
                password: this.twitter_password,
            });
            this.is_twitter_password_auth_sending = false;
            if (result === false) {
                return;
            }

            // 
            await this.userStore.fetchUser(true);
            if (this.userStore.user === null) {
                Message.error('');
                return;
            }

            //  Twitter  updated_at 
            //  updated_at  updated_at 
            // ref: https://stackoverflow.com/a/12192544/17124142 (ISO8601 )
            const current_twitter_account = [...this.userStore.user.twitter_accounts].sort((a, b) => {
                return (a.updated_at < b.updated_at) ? 1 : ((a.updated_at > b.updated_at) ? -1 : 0);
            })[0];

            Message.success(`Twitter @${current_twitter_account.screen_name} `);

            // 
            (this.$refs.twitter_form as any).reset();
            this.twitter_password_auth_dialog = false;
        },

        async logoutTwitterAccount(screen_name: string) {

            // Twitter  API 
            const result = await Twitter.logoutAccount(screen_name);
            if (result === false) {
                return;
            }

            // 
            await this.userStore.fetchUser(true);

            Message.success(`Twitter @${screen_name} `);
        },
    }
});

</script>
<style lang="scss" scoped>

.settings__content {
    opacity: 1;
    transition: opacity 0.4s;

    &--loading {
        opacity: 0;
    }
}

.twitter-accounts {
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
    border-radius: 15px;
    background: var(--v-background-lighten2);
    @include smartphone-horizontal {
        padding: 16px 20px;
        border-radius: 10px;
    }
    @include smartphone-vertical {
        padding: 16px 12px;
        border-radius: 10px;
    }

    &__heading {
        display: flex;
        align-items: center;
        font-size: 18px;
        font-weight: bold;
    }

    &__guide {
        display: flex;
        align-items: center;

        .text-h6 {
            @include tablet-vertical {
                font-size: 19px !important;
            }
            @include smartphone-vertical {
                font-size: 17px !important;
            }
        }

        svg {
            @include smartphone-horizontal-short {
                display: none;
            }
            @include smartphone-vertical {
                display: none;
            }
        }
        svg + div {
            @include smartphone-horizontal-short {
                margin-left: 0px !important;
            }
            @include smartphone-vertical {
                margin-left: 0px !important;
            }
        }
    }

    .twitter-account {
        display: flex;
        align-items: center;
        margin-top: 20px;
        @include smartphone-horizontal {
            margin-top: 16px;
        }

        &__icon {
            flex-shrink: 0;
            width: 70px;
            height: 70px;
            margin-right: 16px;
            border-radius: 50%;
            object-fit: cover;
            // 
            background: linear-gradient(150deg, var(--v-gray-base), var(--v-background-lighten2));
            // 
            // ref: https://sho-log.com/chrome-image-blurred/
            image-rendering: -webkit-optimize-contrast;
            @include smartphone-horizontal {
                width: 52px;
                height: 52px;
            }
            @include smartphone-vertical {
                width: 48px;
                height: 48px;
                margin-right: 10px;
            }
        }

        &__info {
            display: flex;
            flex-direction: column;
            min-width: 0;
            margin-right: 16px;
            @include smartphone-vertical {
                margin-right: 10px;
            }

            &-name {
                display: inline-flex;
                align-items: center;

                &-text {
                    display: inline-block;
                    color: var(--v-text-base);
                    font-size: 20px;
                    font-weight: bold;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;  //   
                    @include smartphone-horizontal {
                        font-size: 18px;
                    }
                    @include smartphone-vertical {
                        font-size: 16px;
                    }
                }
            }

            &-screen-name {
                display: inline-block;
                color: var(--v-text-darken1);
                font-size: 16px;
                @include smartphone-horizontal {
                    font-size: 14px;
                }
                @include smartphone-vertical {
                    font-size: 13.5px;
                }
            }
        }

        &__login {
            margin-top: 20px;
            margin-left: auto;
            margin-right: auto;
            border-radius: 7px;
            font-size: 15px;
            letter-spacing: 0;
            @include tablet-vertical {
                height: 42px !important;
                font-size: 14.5px;
            }
            @include smartphone-horizontal {
                height: 42px !important;
                font-size: 14.5px;
            }
            @include smartphone-vertical {
                height: 42px !important;
                font-size: 14.5px;
            }
        }

        &__logout {
            background: var(--v-gray-base);
            border-radius: 7px;
            font-size: 15px;
            letter-spacing: 0;
            @include smartphone-horizontal {
                width: 116px !important;
            }
            @include smartphone-vertical {
                width: 100px !important;
                height: 48px !important;
                border-radius: 5px;
                font-size: 14px;
                svg {
                    width: 20px;
                    margin-right: 4px !important;
                }
            }
        }
    }
}

</style>