<template>
    <!--  -->
    <SettingsBase>
        <h2 class="settings__heading">
            <router-link v-ripple class="settings__back-button" to="/settings/">
                <Icon icon="fluent:arrow-left-12-filled" width="25px" />
            </router-link>
            <Icon icon="fluent:person-20-filled" width="25px" />
            <span class="ml-2"></span>
        </h2>
        <div class="settings__content" :class="{'settings__content--loading': is_loading}">
            <div class="account" v-if="userStore.user === null">
                <div class="account-wrapper">
                    <img class="account__icon" src="/assets/images/account-icon-default.png">
                    <div class="account__info">
                        <div class="account__info-name">
                            <span class="account__info-name-text"></span>
                        </div>
                        <span class="account__info-id">Not logged in</span>
                    </div>
                </div>
                <v-btn class="account__login ml-auto" color="secondary" width="140" height="56" depressed to="/login/">
                    <Icon icon="fa:sign-in" class="mr-2" />
                </v-btn>
            </div>
            <div class="account" v-if="userStore.user !== null">
                <div class="account-wrapper">
                    <img class="account__icon" :src="userStore.user_icon_url ?? ''">
                    <div class="account__info">
                        <div class="account__info-name">
                            <span class="account__info-name-text">{{userStore.user.name}}</span>
                            <span class="account__info-admin" v-if="userStore.user.is_admin"></span>
                        </div>
                        <span class="account__info-id">User ID: {{userStore.user.id}}</span>
                    </div>
                </div>
                <v-btn class="account__login ml-auto" color="secondary" width="140" height="56" depressed @click="userStore.logout()">
                    <Icon icon="fa:sign-out" class="mr-2" />
                </v-btn>
            </div>
            <div class="account-register" v-if="userStore.is_logged_in === false">
                <div class="account-register__heading">
                    KonomiTV <br>
                </div>
                <div class="account-register__feature">
                    <div class="account-feature">
                        <Icon class="account-feature__icon" icon="bi:chat-left-text-fill" />
                        <div class="account-feature__info">
                            <span class="account-feature__info-heading"></span>
                            <span class="account-feature__info-text"></span>
                        </div>
                    </div>
                    <div class="account-feature">
                        <Icon class="account-feature__icon" icon="fa-brands:twitter" />
                        <div class="account-feature__info">
                            <span class="account-feature__info-heading">Twitter </span>
                            <span class="account-feature__info-text"> Twitter Twitter </span>
                        </div>
                    </div>
                    <div class="account-feature">
                        <Icon class="account-feature__icon" icon="fluent:arrow-sync-20-filled" />
                        <div class="account-feature__info">
                            <span class="account-feature__info-heading"></span>
                            <span class="account-feature__info-text"></span>
                        </div>
                    </div>
                    <div class="account-feature">
                        <Icon class="account-feature__icon" icon="fa-solid:sliders-h" />
                        <div class="account-feature__info">
                            <span class="account-feature__info-heading"></span>
                            <span class="account-feature__info-text"></span>
                        </div>
                    </div>
                </div>
                <div class="account-register__description">
                    KonomiTV <br class="smartphone-vertical-only"><br>
                    <br class="smartphone-vertical-only"> KonomiTV <br>
                    <br>
                </div>
                <v-btn class="account-register__button" color="secondary" width="100%" max-width="250" height="50" depressed to="/register/">
                    <Icon icon="fluent:person-add-20-filled" class="mr-2" height="24" />
                </v-btn>
            </div>
            <div v-if="userStore.is_logged_in === true">
                <div class="settings__item settings__item--switch">
                    <label class="settings__item-heading" for="sync_settings"></label>
                    <label class="settings__item-label" for="sync_settings">
                        KonomiTV <br>
                        <br>
                        <br>
                    </label>
                    <v-switch class="settings__item-switch" id="sync_settings" inset hide-details
                        v-model="sync_settings">
                    </v-switch>
                </div>
                <v-dialog max-width="530" v-model="sync_settings_dialog">
                    <v-card>
                        <v-card-title class="justify-center"></v-card-title>
                        <v-card-text>
                            <br>
                            <br>
                        </v-card-text>
                        <div class="d-flex flex-column px-4 pb-4 settings__conflict-dialog">
                            <v-btn class="settings__save-button error--text text--lighten-1" depressed
                                @click="overrideServerSettingsFromClient()">
                                <Icon icon="fluent:document-arrow-up-16-filled" class="mr-2" height="22px" />
                                <br class="smartphone-vertical-only">
                            </v-btn>
                            <v-btn class="settings__save-button error--text text--lighten-1 mt-3" depressed
                                @click="overrideClientSettingsFromServer()">
                                <Icon icon="fluent:document-arrow-down-16-filled" class="mr-2" height="22px" />
                                <br class="smartphone-vertical-only">
                            </v-btn>
                            <v-btn class="settings__save-button mt-3" depressed @click="sync_settings_dialog = false">
                                <Icon icon="fluent:dismiss-16-filled" class="mr-2" height="22px" />
                                
                            </v-btn>
                        </div>
                    </v-card>
                </v-dialog>
                <v-form class="settings__item" ref="settings_username" @submit.prevent>
                    <div class="settings__item-heading"></div>
                    <div class="settings__item-label">
                        KonomiTV <br>
                         KonomiTV <br>
                    </div>
                    <v-text-field class="settings__item-form" outlined placeholder=""
                        :dense="is_form_dense"
                        v-model="settings_username"
                        :rules="[settings_username_validation]">
                    </v-text-field>
                </v-form>
                <v-btn class="settings__save-button" depressed @click="updateAccountInfo('username')">
                    <Icon icon="fluent:save-16-filled" class="mr-2" height="24px" />
                </v-btn>
                <v-form class="settings__item" @submit.prevent>
                    <div class="settings__item-heading"></div>
                    <div class="settings__item-label">
                        KonomiTV <br>
                         400400 <br>
                    </div>
                    <v-file-input class="settings__item-form" outlined hide-details placeholder=""
                        :dense="is_form_dense"
                        accept="image/jpeg, image/png"
                        prepend-icon=""
                        prepend-inner-icon="mdi-paperclip"
                        v-model="settings_icon">
                    </v-file-input>
                </v-form>
                <v-btn class="settings__save-button mt-5" depressed @click="updateAccountIcon()">
                    <Icon icon="fluent:save-16-filled" class="mr-2" height="24px" />
                </v-btn>
                <v-form class="settings__item" ref="settings_password" @submit.prevent>
                    <div class="settings__item-heading"></div>
                    <div class="settings__item-label">
                        KonomiTV <br>
                    </div>
                    <v-text-field class="settings__item-form" outlined placeholder=""
                        :dense="is_form_dense"
                        v-model="settings_password"
                        :type="settings_password_showing ? 'text' : 'password'"
                        :append-icon="settings_password_showing ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[settings_password_validation]"
                        @click:append="settings_password_showing = !settings_password_showing">
                    </v-text-field>
                </v-form>
                <v-btn class="settings__save-button" depressed @click="updateAccountInfo('password')">
                    <Icon icon="fluent:save-16-filled" class="mr-2" height="24px" />
                </v-btn>
                <v-divider class="mt-6"></v-divider>
                <div class="settings__item mt-6">
                    <div class="settings__item-heading error--text text--lighten-1"></div>
                    <div class="settings__item-label">
                         KonomiTV <br>
                        <b></b><br>
                    </div>
                </div>
                <v-dialog max-width="385" v-model="account_delete_confirm_dialog">
                    <template v-slot:activator="{ on }">
                        <v-btn class="settings__save-button error mt-5" depressed v-on="on">
                            <Icon icon="fluent:delete-16-filled" class="mr-2" height="24px" />
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title class="justify-center pt-6 font-weight-bold"></v-card-title>
                        <v-card-text class="pt-2 pb-0">
                            <br>
                            
                        </v-card-text>
                        <v-card-actions class="pt-4 px-6 pb-5">
                            <v-spacer></v-spacer>
                            <v-btn color="text" text @click="account_delete_confirm_dialog = false"></v-btn>
                            <v-btn color="error" @click="deleteAccount()"></v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </div>
        </div>
    </SettingsBase>
</template>
<script lang="ts">

import { mapStores } from 'pinia';
import { defineComponent } from 'vue';

import Message from '@/message';
import Settings from '@/services/Settings';
import useSettingsStore from '@/stores/SettingsStore';
import useUserStore from '@/stores/UserStore';
import Utils from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

export default defineComponent({
    name: 'Settings-Account',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            is_loading: true,

            // 
            //  data 
            settings_username: null as string | null,
            settings_username_validation: (value: string | null) => {
                if (value === '' || value === null) return '';
                if (/^.{2,}$/.test(value) === false) return '2';
                return true;
            },
            settings_password: null as string | null,
            settings_password_showing: false,
            settings_password_validation: (value: string | null) => {
                if (value === '' || value === null) return '';
                // : https://qiita.com/grrrr/items/0b35b5c1c98eebfa5128
                if (/^[a-zA-Z0-9!-/:-@[-`{-~]{4,}$/.test(value) === false) return '4';
                return true;
            },

            // 
            settings_icon: null as File | null,

            // 
            account_delete_confirm_dialog: false,

            // 
            sync_settings: useSettingsStore().settings.sync_settings as boolean,

            // 
            sync_settings_dialog: false,
        };
    },
    computed: {
        ...mapStores(useSettingsStore, useUserStore),
    },
    watch: {
        // sync_settings 
        async sync_settings() {

            //  & 
            if (this.sync_settings === true && this.sync_settings_dialog === false) {

                // 
                const sync_settings = this.settingsStore.getSyncableClientSettings();

                //  JSON 
                const sync_settings_json = JSON.stringify(sync_settings);

                // 
                //  JSON 
                const server_sync_settings = await Settings.fetchClientSettings();
                if (server_sync_settings === null) {
                    Message.error('');
                    return;
                }
                const server_sync_settings_json = JSON.stringify(server_sync_settings);

                // =
                if (sync_settings_json !== server_sync_settings_json) {

                    // 
                    this.sync_settings_dialog = true;
                    this.sync_settings = false;

                // 
                } else {

                    // 
                    this.settingsStore.settings.sync_settings = true;
                }

            //  & 
            } else if (this.sync_settings === false && this.sync_settings_dialog === false) {
                this.settingsStore.settings.sync_settings = false;
            }
        }
    },
    async created() {

        // 
        await this.userStore.fetchUser();

        // 
        this.is_loading = false;
    },
    methods: {

        // 
        async overrideServerSettingsFromClient() {

            // 
            await this.settingsStore.syncClientSettingsToServer(true);

            // 
            this.settingsStore.settings.sync_settings = true;
            this.sync_settings = true;

            // 
            this.sync_settings_dialog = false;
        },

        // 
        async overrideClientSettingsFromServer() {

            // 
            // 
            await this.settingsStore.syncClientSettingsFromServer(true);

            // 
            // 
            //  syncClientSettingsFromServer(true) 
            // overrideServerSettingsFromClient() 
            this.settingsStore.settings.sync_settings = true;
            this.sync_settings = true;

            // 
            this.sync_settings_dialog = false;
        },

        async updateAccountInfo(update_type: 'username' | 'password') {

            // 
            // ref: https://qiita.com/Hijiri_Ishi/items/56cac99c8f3806a6fa24
            if (update_type === 'username') {
                if ((this.$refs.settings_username as any).validate() === false) return;
            } else {
                if ((this.$refs.settings_password as any).validate() === false) return;
            }

            //  () 
            if (update_type === 'username') {
                if (this.settings_username === null) return;
                await this.userStore.updateUser({username: this.settings_username});
            } else {
                if (this.settings_password === null) return;
                await this.userStore.updateUser({password: this.settings_password});
            }
        },

        async updateAccountIcon() {

            // 
            if (this.settings_icon === null) {
                Message.error('');
                return;
            }

            //  () 
            await this.userStore.updateUserIcon(this.settings_icon);
        },

        async deleteAccount() {

            // 
            this.account_delete_confirm_dialog = false;

            //  () 
            await this.userStore.deleteUser();
        }
    }
});

</script>
<style lang="scss" scoped>

.settings__content {
    opacity: 1;
    transition: opacity 0.4s;

    &--loading {
        opacity: 0;
    }
}

.settings__conflict-dialog {
    .v-btn {
        @include smartphone-vertical {
            height: 50px !important;
            text-align: left;
        }
        br.smartphone-vertical-only {
            display: none;
            @include smartphone-vertical {
                display: inline;
            }
        }
    }
}

.account {
    display: flex;
    align-items: center;
    height: 130px;
    padding: 18px 20px;
    border-radius: 15px;
    background: var(--v-background-lighten2);
    @include tablet-horizontal {
        align-items: normal;
        flex-direction: column;
        height: auto;
        padding: 16px;
    }
    @include tablet-vertical {
        align-items: normal;
        flex-direction: column;
        height: auto;
        padding: 16px;
    }
    @include smartphone-horizontal {
        align-items: normal;
        flex-direction: column;
        height: auto;
        padding: 16px;
        border-radius: 10px;
    }
    @include smartphone-vertical {
        align-items: normal;
        flex-direction: column;
        height: auto;
        padding: 16px 12px;
        border-radius: 10px;
    }

    &-wrapper {
        display: flex;
        align-items: center;
        min-width: 0;
        height: 94px;
        @include smartphone-horizontal {
            height: 80px;
        }
        @include smartphone-vertical {
            height: 70px;
        }
    }

    &__icon {
        flex-shrink: 0;
        min-width: 94px;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
        // 
        background: linear-gradient(150deg, var(--v-gray-base), var(--v-background-lighten2));
        // 
        // ref: https://sho-log.com/chrome-image-blurred/
        image-rendering: -webkit-optimize-contrast;
        @include smartphone-horizontal {
            min-width: 80px;
        }
        @include smartphone-vertical {
            min-width: 70px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        min-width: 0;
        margin-left: 20px;
        margin-right: 12px;
        @include smartphone-vertical {
            margin-left: 12px !important;
            margin-right: 0 !important;
        }

        &-name {
            display: inline-flex;
            align-items: center;
            height: 33px;

            &-text {
                display: inline-block;
                font-size: 23px;
                color: var(--v-text-base);
                font-weight: bold;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;  //   
                @include smartphone-horizontal {
                    font-size: 21px;
                }
                @include smartphone-vertical {
                    font-size: 20px;
                }
            }
        }

        &-admin {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            width: 52px;
            height: 28px;
            margin-left: 10px;
            border-radius: 5px;
            background: var(--v-secondary-base);
            font-size: 14px;
            font-weight: 500;
            line-height: 2;
            @include smartphone-horizontal {
                width: 45px;
                height: 24px;
                border-radius: 4px;
                font-size: 11.5px;
            }
            @include smartphone-vertical {
                width: 45px;
                height: 24px;
                border-radius: 4px;
                font-size: 11.5px;
            }
        }

        &-id {
            display: inline-block;
            margin-top: 2px;
            color: var(--v-text-darken1);
            font-size: 16px;
            @include smartphone-horizontal {
                font-size: 14.5px;
            }
            @include smartphone-vertical {
                font-size: 14.5px;
            }
        }
    }

    &__login {
        border-radius: 7px;
        font-size: 16px;
        letter-spacing: 0;
        @include tablet-horizontal {
            height: 50px !important;
            margin-top: 8px;
            margin-right: auto;
            font-size: 14.5px;
        }
        @include tablet-vertical {
            height: 42px !important;
            margin-top: 8px;
            margin-right: auto;
            font-size: 14.5px;
        }
        @include smartphone-horizontal {
            height: 42px !important;
            margin-top: 8px;
            margin-right: auto;
            font-size: 14.5px;
        }
        @include smartphone-vertical {
            height: 42px !important;
            margin-top: 16px;
            margin-right: auto;
            font-size: 14.5px;
        }
    }
}

.account-register {
    display: flex;
    flex-direction: column;
    margin-top: 28px;

    &__heading {
        font-size: 21px;
        font-weight: bold;
        text-align: center;
        font-feature-settings: "palt" 1;  // 
        letter-spacing: 0.04em;  // 
        @include smartphone-horizontal {
            font-size: 19px;
        }
        @include smartphone-vertical {
            font-size: 18px;
        }
    }

    &__feature {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 18px;
        grid-column-gap: 16px;
        margin-top: 28px;
        @include tablet-horizontal {
            grid-template-columns: 1fr;
        }
        @include tablet-vertical {
            grid-template-columns: 1fr;
        }
        @include smartphone-horizontal {
            grid-template-columns: 1fr;
        }
        @include smartphone-vertical {
            grid-template-columns: 1fr;
        }

        .account-feature {
            display: flex;
            align-items: center;

            &__icon {
                width: 46px;
                height: 46px;
                flex-shrink: 0;
                margin-right: 16px;
                color: var(--v-secondary-lighten1);
            }

            &__info {
                display: flex;
                flex-direction: column;
                &-heading {
                    font-size: 15px;
                }
                &-text {
                    margin-top: 3px;
                    color: var(--v-text-darken1);
                    font-size: 12.5px;
                    line-height: 1.65;
                }
            }
        }
    }

    &__description {
        margin-top: 32px;
        font-size: 15px;
        line-height: 1.7;
        text-align: center;
        br.smartphone-vertical-only {
            display: none;
        }
        @include tablet-horizontal {
            font-size: 12.5px;
        }
        @include tablet-vertical {
            font-size: 10.5px;
        }
        @include smartphone-horizontal {
            font-size: 12.5px;
        }
        @include smartphone-horizontal-short {
            font-size: 10.5px;
        }
        @include smartphone-vertical {
            font-size: 12.5px;
            br.smartphone-vertical-only {
                display: inline;
            }
        }
    }

    &__button {
        margin-top: 24px;
        margin-left: auto;
        margin-right: auto;
        border-radius: 7px;
        font-size: 16px;
        letter-spacing: 0;
        @include tablet-vertical {
            height: 42px !important;
            font-size: 14.5px;
        }
        @include smartphone-horizontal {
            height: 42px !important;
            font-size: 14.5px;
        }
        @include smartphone-vertical {
            max-width: 210px !important;
            height: 42px !important;
            font-size: 15px;
        }
    }
}

</style>